import { useState } from "react";
export class VanillaState {
    constructor(rerender) {
        this.__rerender = rerender;
    }
}
export function useVanillaState(CustomState) {
    // state that induces rerenders:
    const [, setNum] = useState(0);
    // stable ref for state instance:
    const [instance] = useState(() => {
        const rerenderInducer = () => {
            setNum((num) => num + 1);
        };
        return new CustomState(rerenderInducer);
    });
    return instance;
}
export function rerender(target, name, descriptor) {
    const original = descriptor.value;
    if (typeof original === "function") {
        descriptor.value = function (...args) {
            if (this instanceof VanillaState) {
                try {
                    const result = original.apply(this, args);
                    this.__rerender();
                    return result;
                }
                catch (e) {
                    throw e;
                }
            }
            else {
                throw Error("Can only use @rerender on class methods that extend VanillaState");
            }
        };
    }
    return descriptor;
}
